import {
  FormItem,
  Input,
  NativeSelect,
  Textarea,
  Button,
  Select
} from "@vkontakte/vkui";

import { options } from "../../../constants/select-options";
import { useState } from "react";
import { createBookFX } from "../../../api/server/books/books";

import ImageInput from "../components/ImageInput";

export default function CreateBook() {

  const [title, setTitle] = useState<string>('');
  const [author, setAuthor] = useState<string>('');
  const [quality, setQuality] = useState<string>('');
  const [genre, setGenre] = useState<string>('');
  const [isbn, setIsbn] = useState<string>('');
  const [descr, setDescr] = useState<string>('');

  const setData = () => {
    return {
      bookTitle: title,
      bookAuthor: author,
      bookQuality: quality,
      bookGanre: genre,
      bookIsbn: isbn,
      bookDesr: descr
    }
  }

  const handleSubmit = (e: any) => {
    e.preventDefault();
    createBookFX(setData());

    setTitle('');
    setAuthor(''),
    setQuality('');
    setGenre('');
    setIsbn('');
    setDescr('');
  }

  return (
    <form onSubmit={handleSubmit}>
      {/*   */}
      <FormItem>
        <ImageInput />
      </FormItem>
      {/*   */}
      <FormItem
        htmlFor="bookTitle"
        top=''
      >
        <Input
          id="bookTitle"
          placeholder=" "
          type="text"
          name="title"
          required
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
      </FormItem>
      {/*   */}
      <FormItem
        top=''
        htmlFor="bookAuthor"
      >
        <Input
          id="bookAuthor"
          placeholder="  "
          type="text"
          name="author"
          required
          value={author}
          onChange={(e) => setAuthor(e.target.value)}
        />
      </FormItem>
      {/*   */}
      <FormItem
        top=''
        htmlFor="bookQuality"
      >
        <Select
          id="bookQuality"
          placeholder="  "
          name="quality"
          options={options}
          value={quality}
          onChange={(e) => setQuality(e.target.value)}
        />
      </FormItem>
      {/*   */}
      <FormItem
        top=''
        htmlFor="bookQuality"
      >
        <NativeSelect
          id="bookGenre"
          placeholder="  "
          name="genre"
          value={genre}
          onChange={(e) => setGenre(e.target.value)}
        >
          <option value="bad">   </option>
        </NativeSelect>
      </FormItem>
      {/* ISBN  */}
      <FormItem
        top='ISBN'
        htmlFor="bookIsbn"
      >
        <Input
          id="bookIsbn"
          placeholder=" ISBN "
          type="text"
          name="isbn"
          required
          value={isbn}
          onChange={(e) => setIsbn(e.target.value)}
        />
      </FormItem>
      {/*    */}
      <FormItem
        top=''
        htmlFor="bookDescription"
      >
        <Textarea
          id="bookDescription"
          placeholder=" "
          name="description"
          value={descr}
          onChange={(e) => setDescr(e.target.value)}
        />
      </FormItem>
      {/*    */}
      <FormItem>
        <Button
          type="submit"
          stretched
          size="l"
        ></Button>
      </FormItem>
    </form >
  )
}